.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.karo {
  background-color: hsl(34, 53%, 82%);
  background-image: repeating-linear-gradient(45deg, transparent 5px, hsla(197, 62%, 11%, 0.5) 5px, hsla(197, 62%, 11%, 0.5) 10px,
    hsla(5, 53%, 63%, 0) 10px, hsla(5, 53%, 63%, 0) 35px, hsla(5, 53%, 63%, 0.5) 35px, hsla(5, 53%, 63%, 0.5) 40px,
    hsla(197, 62%, 11%, 0.5) 40px, hsla(197, 62%, 11%, 0.5) 50px, hsla(197, 62%, 11%, 0) 50px, hsla(197, 62%, 11%, 0) 60px,
    hsla(5, 53%, 63%, 0.5) 60px, hsla(5, 53%, 63%, 0.5) 70px, hsla(35, 91%, 65%, 0.5) 70px, hsla(35, 91%, 65%, 0.5) 80px,
    hsla(35, 91%, 65%, 0) 80px, hsla(35, 91%, 65%, 0) 90px, hsla(5, 53%, 63%, 0.5) 90px, hsla(5, 53%, 63%, 0.5) 110px,
    hsla(5, 53%, 63%, 0) 110px, hsla(5, 53%, 63%, 0) 120px, hsla(197, 62%, 11%, 0.5) 120px, hsla(197, 62%, 11%, 0.5) 140px
    ),
  repeating-linear-gradient(135deg, transparent 5px, hsla(197, 62%, 11%, 0.5) 5px, hsla(197, 62%, 11%, 0.5) 10px,
    hsla(5, 53%, 63%, 0) 10px, hsla(5, 53%, 63%, 0) 35px, hsla(5, 53%, 63%, 0.5) 35px, hsla(5, 53%, 63%, 0.5) 40px,
    hsla(197, 62%, 11%, 0.5) 40px, hsla(197, 62%, 11%, 0.5) 50px, hsla(197, 62%, 11%, 0) 50px, hsla(197, 62%, 11%, 0) 60px,
    hsla(5, 53%, 63%, 0.5) 60px, hsla(5, 53%, 63%, 0.5) 70px, hsla(35, 91%, 65%, 0.5) 70px, hsla(35, 91%, 65%, 0.5) 80px,
    hsla(35, 91%, 65%, 0) 80px, hsla(35, 91%, 65%, 0) 90px, hsla(5, 53%, 63%, 0.5) 90px, hsla(5, 53%, 63%, 0.5) 110px,
    hsla(5, 53%, 63%, 0) 110px, hsla(5, 53%, 63%, 0) 140px, hsla(197, 62%, 11%, 0.5) 140px, hsla(197, 62%, 11%, 0.5) 160px
  );
  margin: '5%';
  border-radius: '5px';
  height: '92%';
  border-color: 'black';
  border-style: 'solid';
  border-width: '1px';
}

.stock { grid-area: stock; }
.waste { grid-area: waste; }
.spacer { grid-area: spacer; }

.f0 { grid-area: f0; }
.f1 { grid-area: f1; }
.f2 { grid-area: f2; }
.f3 { grid-area: f3; }

.t0 { grid-area: t0; }
.t1 { grid-area: t1; }
.t2 { grid-area: t2; }
.t3 { grid-area: t3; }
.t4 { grid-area: t4; }
.t5 { grid-area: t5; }
.t6 { grid-area: t6; }

.footer { grid-area: footer; }

.grid-container {
  display: grid;
  grid-template-areas:
    'stock waste spacer f0 f1 f2 f3'
    't0 t1 t2 t3 t4 t5 t6'
    'footer footer footer footer footer footer footer';
  grid-gap: 10px;
  background-color: 'darkgreen';
 
  padding: 10px;
}

.tlicon { grid-area: tlicon; position: relative; }
.tlface { grid-area: tlface; position: relative;}
.tspacer { grid-area: tspacer;position: relative; }
.tricon { grid-area: tricon;position: relative;  }
.mainface { grid-area: mainface; text-align: center; position: relative; font-size: 5vw;}
.blicon { grid-area: blicon; vertical-align: bottom;position: relative;}
.bspacer { grid-area: bspacer;position: relative; }
.brface { grid-area: brface; position: relative;}
.bricon { grid-area: bricon; position: relative;}
.lcontent {
  left: 0;position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.rcontent {
  right: 0;position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.mcontent {
  position: absolute;
  top:50%;
  left:50%;
  
  
  -ms-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

.card-grid-container {
  padding-top: '5px';
  display: grid;
  font-size: 2vw;
  grid-template-rows: 2.1vw 8.3vw 2.1vw;
  grid-template-areas:
    'tlicon tlface tspacer tricon'
    'mainface mainface mainface mainface'
    'blicon bspacer brface bricon';
  grid-gap: 0.2vw;
  
}
